@use '../Functios.scss' as f;

.cart-page {
  opacity: 0;
  transform: translate3d(-30px, 0px, 0px);
  animation-name: showLeft;
  animation-duration: 0.3s;
  animation-fill-mode: forwards;

  h1 {
    font-weight: 300;
    text-align: center;
    margin: 1rem 0 2rem;
  }

  &__table {
    .grid {
      display: grid;
      grid-template-columns: 1fr repeat(3, 100px);
      justify-items: center;
      align-items: center;
      @media screen and (max-width: 767px) {
        grid-template-columns: 1fr;
      }
    }

    .t-head {
      @media screen and (max-width: 767px) {
        display: none;
      }

      p {
        text-transform: uppercase;
        &:first-of-type {
          justify-self: start;
          margin-left: 1rem;
        }
      }
    }

    .subtotal {
      padding-top: 1rem;
      display: flex;
      align-items: flex-start;

      & > div {
        margin-left: auto;
        display: grid;
        grid-template-columns: 1fr;
        gap: 1rem;
      }

      .total-price {
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
    }
  }
}
